.Header {
    background-color: rgb(33, 33, 45);
    color: white;
    width: 100%;
    height: 600px;
    display: flex;

    .LeftHeader {
        background-color: rgb(33, 33, 45);
        height: 100%;
        width: 60%;

        .BlockLogo {
            width: 100%;
            height: 25%;
            background-color: rgb(33, 33, 45);
            align-items: center;
            flex-wrap: nowrap;
            justify-content: center;
            display: flex;

            .icon {
                height: 60px;
                width: 60px;
                border-radius: 50%;
              }

              .p {
                color: white;
                font-size: 25px;
                font-family: Arial, Helvetica, sans-serif;
                font-weight: bold;
                font-family: 'proxima',Arial,sans-serif;
                border-color: transparent;
                border-style: solid;
                margin-left: 10px;
              }
        }

        .BlockText {
            width: 100%;
            height: 50%;
            background-color: rgb(33, 33, 45);
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;

            .Text {
                color: #31EFA5;
                font-size: 55px;
                font-weight: bold;
                font-family: 'proxima',Arial,sans-serif;
                text-transform: uppercase;
                border-color: transparent;
                border-style: solid;
                margin: 0;
              }
    
            .Text1 {
                color: white;
                font-size: 80px;
                font-weight: bold;
                font-family: 'proxima',Arial,sans-serif;
                text-transform: uppercase;
                border-color: transparent;
                border-style: solid;
                margin: 0;
              }
        }

        .BlockPlan {
            width: 100%;
            height: 25%;
            background-color: rgb(33, 33, 45);
            display: flex;
            align-items: center;
            justify-content: space-evenly;

            & button {
                border: 1px solid #31EFA5; 
                background-color: transparent; 
                color: white;
                border-radius: 30px;             
                box-shadow:0 0 5px #31EFA5;
                font-size: 25px;
                font-family: Arial, Helvetica, sans-serif;
                font-weight: bold;
                padding: 15px 30px;

                .link {
                    color:white;
                    text-decoration: none;
                }
              }
              & button:hover {
                cursor: pointer;
                background-color: #31EFA5;
              }
              // & button:active {
              //   background-color: #31EFA5;
              // }
        }

        
    }

    .RightHeader {
        background-color: rgb(33, 33, 45);
        height: 100%;
        width: 40%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
        position: relative;

        .krug {
            border-radius: 50%;
            box-shadow:0 0 90px rgba(48, 239, 165, 0.5);
            border: 1px solid #31EFA5; 
            width: 80%;
            height: 80%;
            left: 10px;
            float: right;
            position: absolute;
            
        }


        .pict {
          background-image: url(../../../items/photo.png);
          background-repeat: no-repeat;
          background-size: contain;
          vertical-align: bottom;
          // background-position: right bottom;
          width: 100%;
          height: 100%;
        }

        .img {
            width: 100%;
            max-width: 40%;
            float: right;
            height: 600px;
            position: absolute;
            background-blend-mode: multiply;
        }
        
    }
}
/////////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: 1400px) {
    .Header {
      height: 550px;
      .RightHeader {
        .krug {
          height: 80%;
          width: 80%;
        }
      }
    }
}
////////////////////////////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: 1200px) {
    .Header {
      height: 500px;

      .LeftHeader {
        width: 720px;
        
        .BlockText {
            margin: 0;
            .Text, .Text1 {
                font-size: 48px;
            }
        }

        .BlockPlan {
            margin: 0;
            
            & button {
                font-size: 24px;
            }
        }
      }

      .RightHeader {
        width: 480px;


        .krug {
            width: 399px;
            height: 399px;
        }
        .pict {
            width: 480px;
        }
      }
    }
  }
// //  ////////////////////////////////////////////////////////////////////////////////////////////////// 
  @media screen and (max-width: 1024px) {
    .Header {
      height: 450px;

      .LeftHeader {
        width: 55%;

        .BlockLogo {
            margin: 0;

            .icon {
                height: 40px;
                width: 40px;
                border-radius: 50%;
              }

              .p {
                color: white;
                font-size: 22px;
                font-family: Arial, Helvetica, sans-serif;
                font-weight: bold;
                font-family: 'proxima',Arial,sans-serif;
                border-color: transparent;
                border-style: solid;
                margin-left: 10px;
              }
            }

            .BlockText {
                margin: 0;
                .Text, .Text1 {
                    font-size: 37px;
                }
            }

            .BlockPlan {
                margin: 0;

                & button {
                    font-size: 16px;
                }
            }
      }

      .RightHeader {
        width: 45%;


        .krug {
            width: 339px;
            height: 339px;
        }
        // .pict {
        //     width: 400px;
        //     height: 500px;
        // }
    }
    }
  }
//   ////////////////////////////////////////////////////////////////////////////////////////////
  @media screen and (max-width: 815px) {
    .Header {
      height: 400px;

      .LeftHeader {
        width: 50%;
        margin: 0;

        .BlockPlan {
            margin: 0;

            & button {
                font-size: 13px;
                padding: 10px 20px;
            }
        }

        .BlockText {
            margin: 0;

            .Text, .Text1 {
                font-size: 27px;
            }
        }
      }

      .RightHeader {
        margin: 0;
        width: 50%;
      }
    }
  }
/////////////////////////////////////////////////////////////////////////////////
@media screen and (max-width: 700px) {
  .Header {
    .LeftHeader {
      width: 100%;

      .BlockPlan {

        & button {
            font-size: 20px;
            padding: 10px 20px;
        }
    }

    .BlockText {
        margin: 0;

        .Text, .Text1 {
            font-size: 45px;
        }
    }
    }
    .RightHeader {
      display: none;
    }
  }
  
}
////////////////////////////////////////////////////////////////////////////
  @media screen and (max-width: 480px) {
    .Header {
      .LeftHeader {
        width: 100%;

        .BlockLogo {
          .p {
            font-size: 18px;
          }
        }

        .BlockPlan {

          & button {
              font-size: 17px;
              padding: 10px 10px;
          }
      }

      .BlockText {
          margin: 0;

          .Text, .Text1 {
              font-size: 35px;
          }
      }
      }
      .RightHeader {
        display: none;
      }
    }
    
  }
// ///////////////////////////////////////////////////////////////////////////////////////////////  
 @media screen and (max-width: 350px) {
  .Header {
    .LeftHeader {
      .BlockLogo {
        .p {
          font-size: 14px;
        }
      }
      .BlockPlan {
        flex-direction: column;
        & button {
          font-size: 15px;
          margin-bottom: 5px;
          padding: 10px 20px;
        }
      }

      .BlockText {
        .Text, .Text1 {
          font-size: 30px;
        }
      }
    }
  }
 }

